.App {
    text-align: center;
}

.label {
    border-radius: 3px;
    background-color: khaki;
    font-weight: bold;
    padding: 5px;
    width: 125px;
}

.highlight {
    padding: 2px;
    border: 4px solid red;
    border-radius: 4px;
}

.image {
    position: relative;
    margin-bottom: 40px;
}

.image img {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.filter {
    padding: 5px;
}

.label-select {
    margin-left: 10px;
    display: inline-block;
    width: 400px;
}

header {
    display: flex;
    justify-content: center;
    background-color: bisque;
    padding: 10px;
    background-color: gold;
    color: darkgreen;
}
